<template>
  <div class="container">
    <h2 class="text-center">Á≥ªÁµ±‰ΩøÁî®ÁãÄÊÖãÁ¥ÄÈåÑ</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/examLobby')">ËøîÂõûÂ§ßÂª≥</button>
    <p v-if="systemLogData.length === 0" class="text-danger fw-bold fs-5 text-center">ÁõÆÂâçÂ∞öÊú™Êúâ‰ΩøÁî®Á¥ÄÈåÑ„ÄÇ</p>
    <ul v-else class="list-group">
      <li v-for="item in systemLogData" class="list-group-item">{{ item.time + " üëâ " + item.content }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "SystemLog",
  components: {},
  data() {
    return {
      systemLogData: [
        // {
        //   time: "113/05/02 1400",
        //   content: "Ë∂ôÂæ∑Êü±Êüì‰∏äÂ≠∏Áøí",
        // },
      ],
    };
  },
  methods: {},
  mounted() {
    this.$http.get("/api/usingLogs/getLogs").then((response) => {
      // Ë™øÊï¥ÊôÇÈñìÊ†ºÂºè
      let data = [];
      response.data.data.forEach((item) => {
        let date = new Date(item.time);
        let year = date.getFullYear() - 1911;
        let month = date.getMonth() + 1 < 10 ? "0" + (date.getMonth() + 1) : date.getMonth() + 1;
        let day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
        let hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
        let minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
        let time = `${year}/${month}/${day} ${hour}:${minute}`;
        data.push({ time, content: item.content });
      });
      // ÁøªËΩâÈô£ÂàóÔºåËÆìÊúÄÊñ∞ÁöÑÁ¥ÄÈåÑÂú®ÊúÄ‰∏äÈù¢
      data.reverse();
      // ÂÑ≤Â≠òÈÄ≤Ë≥áÊñô
      this.systemLogData = data;
    });
  },
  computed: {},
  watch: {},
};
</script>

